import React from "react";
// @Mui
import {
  Box,
  Container,
  Grid,
  Stack,
  Typography,
  useTheme,
} from "@mui/material";
// assets
import service1 from "@/assets/service-1.png";
import service2 from "@/assets/service-2.png";
import Image from "next/image";
import { Icon } from "@iconify/react";

// ----------------------------------------------------------

const ServiceItem = ({ title, description, hideDivider }) => {
  return (
    <Stack direction="row" alignItems="center" gap={2}>
      <Stack gap={1}>
        <Typography
          sx={{ maxWidth: "70%", wordWrap: "break-word" }}
          variant="h4"
        >
          {title}
        </Typography>
        <Typography>
          It is a long established fact that a reader will be
        </Typography>
      </Stack>
      {!hideDivider && (
        <Box
          sx={{
            bgcolor: "grey.500",
            height: "10vh",
            width: 3,
          }}
        />
      )}
    </Stack>
  );
};

// -----------------------------------------------------------

const FeatureItem = ({ icon, title }) => {
  const theme = useTheme();

  return (
    <Stack direction="row" alignItems="center" gap={1}>
      <Icon
        style={{ color: theme.palette.grey[300], width: 30, height: 30 }}
        icon={icon}
      />
      <Typography variant="h4" color="grey.100">
        {title}
      </Typography>
    </Stack>
  );
};

// -----------------------------------------------------------

function Services() {
  return (
    <Box>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Container>
            <Stack gap={6} direction="row" alignItems="center">
              <Stack gap={0} direction="row">
                <Image
                  src={service1}
                  style={{ aspectRatio: "1/1" }}
                  height={200}
                  objectFit="cover"
                  alt="Just a girl services"
                />
                <Image
                  src={service2}
                  height={200}
                  objectFit="cover"
                  alt="Just a girl services"
                />
              </Stack>
              <Stack direction="row" alignItems="center" gap={3}>
                <ServiceItem title="Quality Products" />
                <ServiceItem title="Best Production" />
                <ServiceItem title="100% Authentic" hideDivider />
              </Stack>
              <Box />
            </Stack>
          </Container>
        </Grid>
        <Grid item xs={12}>
          <Box sx={{ bgcolor: "grey.900", width: "100%", py: 3 }}>
            <Stack
              direction="row"
              alignItems="center"
              justifyContent="center"
              gap={3}
            >
              <FeatureItem
                icon="material-symbols:local-shipping"
                title="Free Shipping"
              />
              <FeatureItem />
              <FeatureItem />
            </Stack>
          </Box>
        </Grid>
      </Grid>
    </Box>
  );
}

export default Services;
