import React from "react";
// next
import Image from "next/image";
// @Mui
import { Box, Button, Stack, Typography } from "@mui/material";
// assets
import heroBg from "@/assets/hero-bg.png";
import clouds from "@/assets/cloud.png";

// -----------------------------------------------------------------------------

function HeroBanner() {
  return (
    <Box sx={{ position: "relative", height: "70vh" }}>
      {/* Clouds */}
      <Box
        component="img"
        src="/cloud.svg"
        sx={{
          width: 300,

          position: "absolute",
          left: "-5%",
          zIndex: 3,
          top: 0,
        }}
      />
      <Box
        component="img"
        src="/cloud.svg"
        sx={{
          width: 300,

          position: "absolute",
          left: "30%",
          zIndex: 3,
          bottom: 0,
        }}
      />
      <Box
        component="img"
        src="/cloud.svg"
        sx={{
          width: 300,

          position: "absolute",
          left: "30%",
          zIndex: 3,
          top: "5%",
        }}
      />
      {/* Hero Content */}
      <Box
        sx={{
          position: "absolute",
          height: "100%",
          bgcolor: "#000000",
          width: "40%",
          zIndex: 2,
          filter: "opacity(0.7)",
          px: 3,
          py: 6,
        }}
      >
        <Stack sx={{ height: "100%", zIndex: 10 }} justifyContent="center">
          <Typography sx={{ zIndex: 4 }} variant="h1" color="grey.100">
            Your Ultimate Online Skincare destination.
          </Typography>
          <Typography color="grey.100" variant="body2">
            It is a long established fact that a reader will be distracted by
            the readable content of a page when looking at its layout. The point
          </Typography>
          <Box sx={{ flex: 0.3 }} />
          <Box>
            <Button variant="contained" size="large">
              Shop Now
            </Button>
          </Box>
          <Typography sx={{ mt: 3 }} variant="subtitle1" color="grey.200">
            It is a long established fact that a reader will be distracted{" "}
          </Typography>
        </Stack>
      </Box>
      <Image
        src={heroBg}
        alt="Welcome to just a girl"
        objectFit="cover"
        layout="fill"
      />
    </Box>
  );
}

export default HeroBanner;
